import React, { useState } from "react";
import Modal from "react-bootstrap/Modal";
import addToMailchimp from "gatsby-plugin-mailchimp";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import { utils } from "../../core/helper";
import Loading from "./loading";

const validationSchema = Yup.object().shape({
  email: Yup.string().email("Please Enter a Email").label("Email").required(),
});

const FormModal = (props) => {
  const [loading, setLoading] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const handleSubscribe = async (values) => {
    setLoading(true);
    const { email } = values;
    const resp = await addToMailchimp(email);
    if (resp.result === "success") {
      utils.showSuccssMsg(resp?.msg);
      setLoading(false);
      reset();
    } else {
      utils.showErrMsg(resp?.msg);
      setLoading(false);
    }
  };

  return (
    <>
      <Modal {...props} size="md" className="form_modal_layout">
        <div className="form_modal_wrapper">
          <Modal.Header>
            <Modal.Title>
              Consult at <span className="clr_text">NO COST</span>
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p>
              Secure a cost-free consultation for expert insights and
              personalized advice tailored to your needs. No charges, just{" "}
              <span className="bold_txt">valuable guidance.</span>
            </p>
            <form autoComplete="on" onSubmit={handleSubmit(handleSubscribe)}>
              <input
                {...register("email")}
                className="form_modal_input"
                placeholder="Enter your mail"
                id="email"
                type="email"
                maxLength={50}
              />
              <p
                style={{
                  fontSize: "12px",
                  color: "#FF0000",
                  fontFamily: "Poppins",
                  fontStyle: "normal",
                  fontWeight: 400,
                  lineHeight: "20px",
                }}
              >
                {errors.email?.message}
              </p>
              <button
                disabled={loading}
                className="form_modal_btn"
                type="submit"
              >
                Submit Now {loading && <Loading />}
              </button>
            </form>
          </Modal.Body>
        </div>
      </Modal>
    </>
  );
};

export default FormModal;
